import React, { Component } from "react";
import s1 from "../../images/slide/1.jpg";
import s2 from "../../images/slide/2.jpg";
import s3 from "../../images/slide/3.jpg";
import s4 from "../../images/slide/4.jpg";
import s5 from "../../images/slide/5.jpg";

class Slider extends Component {
  render() {
    return (
      <React.Fragment>
        <div
          id="slider-section"
          className="carousel slide"
          data-ride="carousel"
        >
          <div className="carousel-inner">
            <div className="carousel-item active">
              <img src={s1} className="d-block w-100" alt="..." />
            </div>
            <div className="carousel-item">
              <img src={s2} className="d-block w-100" alt="..." />
            </div>
            <div className="carousel-item">
              <img src={s3} className="d-block w-100" alt="..." />
            </div>
            <div className="carousel-item">
              <img src={s4} className="d-block w-100" alt="..." />
            </div>
            <div className="carousel-item">
              <img src={s5} className="d-block w-100" alt="..." />
            </div>
          </div>
          <button
            className="carousel-control-prev"
            type="button"
            data-target="#slider-section"
            data-slide="prev"
          >
            <i className="fa fa-arrow-left" aria-hidden="true"></i>
          </button>
          <button
            className="carousel-control-next"
            type="button"
            data-target="#slider-section"
            data-slide="next"
          >
            <i className="fa fa-arrow-right" aria-hidden="true"></i>
          </button>

          <div className="box-info container">
            <div className="content">
              <h2>CHO N TUN L KHAI TRNG</h2>
              <h1>GIM GI CC SC LN N 50%</h1>
              <p>
                Min ph tn hng cc dch v tin ch khch sn: H bi ,Gym
                ,Xng hi, Wifi...
              </p>
              <p>Min ph trang tr phng Honey moon dnh cho cc cp i</p>
              <div className="box-info_button">
                <button className="btn">Khm ph</button>
                <button className="btn outline">gi ngay</button>
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Slider;
